import React, { useState, useEffect } from "react";
import { defaultLimit } from "../../utils/defaultLimit";
import { getPrint, handleSearch } from "../../Redux/PrintCredit/thunk";
import Print from "./Print";
import ListingSkeleton from "../../Component/Skeleton/ListingSkeleton";
import PaginationLimit from "../../Component/Pagination/PaginationLimit";
import { useSelector, useDispatch } from "react-redux";
import { FaPlus } from "react-icons/fa";
import Tooltip from "../../Component/Tooltip/Tooltip";
import { useHistory } from "react-router-dom";
const PrintCreditListing = () => {
  const history = useHistory();
  const prints = useSelector((state) => state.printCredit.prints);
  const loading = useSelector((state) => state.printCredit.loading);
  const loading_updated = useSelector(
    (state) => state.printCredit.loading_updated
  );
  const count = useSelector((state) => state.printCredit.count);
  const search = useSelector((state) => state.search.search);
  const dispatch = useDispatch();

  // for pagination
  const [currentPage, setCurrentPage] = useState(1);
  const [maxPageNumberLimit, setMaxPageNumberLimit] = useState(5);
  const [minPageNumberLimit, setMinPageNumberLimit] = useState(0);
  const [postsPerPage, setPostsPerPage] = useState(10);

  //pagination end
  //loading all the food
  useEffect(() => {
    if (postsPerPage === 0) {
      if (search === "") {
        dispatch(getPrint(count));
      } else {
        dispatch(handleSearch(search, count));
      }
    } else {
      if (search === "") {
        if (postsPerPage === defaultLimit) {
          dispatch(getPrint(postsPerPage));
        } else {
          setCurrentPage(1);
          dispatch(getPrint(postsPerPage));
        }
      } else {
        setCurrentPage(1);
        setMaxPageNumberLimit(5);
        setMinPageNumberLimit(0);
        dispatch(handleSearch(search, postsPerPage));
      }
    }
  }, [dispatch, postsPerPage, search]);
  return (
    <>
      <div className="card">
        <div className="header-content">
          <h4 className="mb-sm-0 fw-normal">Print</h4>
        </div>
        <div className="card-body">
          <div className="row m-0 p-0">
            {prints?.length > 0 && (
              <>
                <div className="col-6">
                  <PaginationLimit
                    postsPerPage={postsPerPage}
                    setPostsPerPage={setPostsPerPage}
                  />
                </div>

                <div className="col-6 text-right">
                  <Tooltip content="Add">
                    <button
                      className="btn btn-primary waves-effect waves-light btn-sm float-end"
                      onClick={() => history.push("/create-print-credit")}
                    >
                      <FaPlus className="mb-1" />
                      &nbsp;Add
                    </button>
                  </Tooltip>
                </div>
              </>
            )}
          </div>
          {loading && <ListingSkeleton />}
          {loading_updated && <ListingSkeleton />}
          {!loading && !loading_updated && (
            <Print
              currentPage={currentPage}
              setCurrentPage={setCurrentPage}
              postsPerPage={postsPerPage}
              setPostsPerPage={setPostsPerPage}
              count={count}
              maxPageNumberLimit={maxPageNumberLimit}
              setMaxPageNumberLimit={setMaxPageNumberLimit}
              minPageNumberLimit={minPageNumberLimit}
              setMinPageNumberLimit={setMinPageNumberLimit}
              search={search}
            />
          )}
        </div>
      </div>
    </>
  );
};

export default PrintCreditListing;
