import jsPDF from "jspdf";
import "../../assets/font";
import Front from "../../assets/Front.png";
import Back from "../../assets/Back.png";
const PrintModal = ({ show, setShow, info, setInfo }) => {
  const {
    name,
    card_no,
    address,
    citizenship_no,
    district,
    mobile_no,
    owner_photo,
    qr,
  } = info;
  let url = "";
  const doc = new jsPDF({
    orientation: "landscape",
    unit: "mm",
    format: [430, 270],
  });
  let nepali_card_number = "";
  for (let letter of card_no) {
    if (letter === "1") {
      nepali_card_number += "!";
    } else if (letter === "2") {
      nepali_card_number += "@";
    } else if (letter === "3") {
      nepali_card_number += "#";
    } else if (letter === "4") {
      nepali_card_number += "$";
    } else if (letter === "5") {
      nepali_card_number += "%";
    } else if (letter === "6") {
      nepali_card_number += "^";
    } else if (letter === "7") {
      nepali_card_number += "&";
    } else if (letter === "8") {
      nepali_card_number += "*";
    } else if (letter === "9") {
      nepali_card_number += "(";
    } else if (letter === "0") {
      nepali_card_number += ")";
    }
  }
  doc.addImage(Front, 0, 0, 430, 270);
  doc.setTextColor("#0E58A4");
  doc.setFont("Preeti", "normal");
  doc.setFontSize(60);
  doc.text(nepali_card_number, 100, 127);
  doc.text("sf8{ g+", 21, 129);
  doc.text("M", 82, 129);
  doc.text("gfd", 21, 150);
  doc.text("M", 82, 150);
  doc.text("7]ufgf", 21, 172);
  doc.text("M", 82, 172);
  doc.text("gf=k|=g+", 21, 193);
  doc.text("M", 82, 193);
  doc.text("lhNnf", 21, 216);
  doc.text("M", 82, 216);
  doc.text("df]afO{n", 21, 237);
  doc.text("M", 82, 237);
  doc.text(name, 100, 150);
  doc.text(address, 100, 172);
  doc.text(citizenship_no, 100, 193);
  doc.text(district?.name, 100, 216);
  doc.text(mobile_no, 100, 237);
  doc.addPage([430, 270], "landscape");
  doc.addImage(Back, 0, 0, 430, 270);
  doc.addImage(owner_photo, 172, 82, 120, 120);
  doc.addImage(qr, 321, 91, 80, 100);
  let blob = doc.output("blob");
  url = URL.createObjectURL(blob);
  const modalClass = show ? "modal display-block" : "modal display-none";
  const handleClose = () => {
    setShow(false);
    setInfo("");
  };
  return (
    <div className={modalClass} tabIndex="-1">
      <div className="modal-dialog modal-md">
        <div className="modal-content content">
          <div className="modal-header">
            <h5 className="modal-title" id="exampleModalLabel">
              Print Preview
            </h5>
            <button
              type="button"
              className="btn-close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={handleClose}
            >
              &times;
            </button>
          </div>
          <div className="modal-body text-center">
            <iframe
              title="print"
              type="application/pdf"
              frameBorder="0"
              width="415px"
              src={url}
              height="580px"
            ></iframe>
          </div>
        </div>
      </div>{" "}
    </div>
  );
};

export default PrintModal;
