import React, { useState } from "react";
import { FaEdit, FaPrint } from "react-icons/fa";

import { useDispatch, useSelector } from "react-redux";
import {
  getParticularPage,
  getPrevious,
  getNext,
} from "../../Redux/PrintCredit/thunk";
import { printCreditConstants } from "../../Redux/PrintCredit/constants";
import PaginationBlock from "../../Component/PaginationBlock";

import NoData from "../../Component/NoData";
import Tooltip from "../../Component/Tooltip/Tooltip";
import { useHistory } from "react-router-dom";
import PrintModal from "./PrintModal";
const Print = ({
  currentPage,
  postsPerPage,
  setCurrentPage,
  count,
  maxPageNumberLimit,
  setMaxPageNumberLimit,
  minPageNumberLimit,
  setMinPageNumberLimit,
  search,
}) => {
  const [show, setShow] = useState(false);
  const [info, setInfo] = useState(null);
  const history = useHistory();
  // props
  const group = useSelector((state) => state.auth.group);
  const is_superuser = useSelector((state) => state.auth.is_superuser);
  const prints = useSelector((state) => state.printCredit.prints);
  const next = useSelector((state) => state.printCredit.next);
  const previous = useSelector((state) => state.printCredit.previous);
  const dispatch = useDispatch();

  // for pagination
  const [pageNumberLimit] = useState(5);

  //change page
  const paginate = (number) => {
    dispatch(getParticularPage({ number, postsPerPage }));
    setCurrentPage(number);
  };

  //handle Click
  const handleClick = (type) => {
    if (type === "previous") {
      dispatch(getPrevious(previous));
      setCurrentPage((prevState) => prevState - 1);
      if ((currentPage - 1) % pageNumberLimit === 0) {
        setMaxPageNumberLimit(maxPageNumberLimit - pageNumberLimit);
        setMinPageNumberLimit(minPageNumberLimit - pageNumberLimit);
      }
    } else if (type === "next") {
      dispatch(getNext(next));
      setCurrentPage((prevState) => prevState + 1);
      if (currentPage + 1 > maxPageNumberLimit) {
        setMaxPageNumberLimit(maxPageNumberLimit + pageNumberLimit);
        setMinPageNumberLimit(minPageNumberLimit + pageNumberLimit);
      }
    }
  };

  //pagination end

  //edit function for the province
  const handleEdit = (id) => {
    dispatch({ type: printCreditConstants.PRINT_EDIT_SUCCESS, payload: id });
    history.push("/create-print-credit");
  };

  const handlePrint = (detail) => {
    setInfo(detail);
    setShow(true);
  };
  const addPermission =
    group === "ADMIN" || group === "STAFF" || is_superuser ? true : false;
  return (
    <>
      {prints?.length > 0 ? (
        <div className="mt-2">
          <div className="table-scrollable">
            <table className="table table-hover table-bordered text-center">
              <thead>
                <tr>
                  <th scope="col" style={{ width: "8%" }}>
                    S.N
                  </th>
                  <th scope="col">Card Number</th>
                  <th scope="col">First Name</th>
                  <th scope="col">Last Name</th>
                  <th scope="col">Action</th>
                </tr>
              </thead>
              <tbody>
                {prints?.length > 0 &&
                  prints.map((detail, i) => {
                    const { id, card_no, first_name_nepali, last_name_nepali } =
                      detail;
                    return (
                      <tr key={id}>
                        <td className="fw-bold" style={{ width: "8%" }}>
                          {postsPerPage * (currentPage - 1) + (i + 1)}{" "}
                        </td>
                        <td>{card_no}</td>
                        <td>
                          {first_name_nepali === "" ? "-" : first_name_nepali}
                        </td>
                        <td>
                          {last_name_nepali === "" ? "-" : last_name_nepali}
                        </td>

                        <td>
                          <Tooltip content="Edit">
                            <button
                              onClick={() => handleEdit(id)}
                              className="btn btn-sm btn-edit btn-primary mx-2"
                            >
                              <FaEdit />
                            </button>
                          </Tooltip>

                          <Tooltip content="Print">
                            <button
                              onClick={() => handlePrint(detail)}
                              className="btn btn-sm  btn-success mx-2"
                            >
                              <FaPrint />
                            </button>
                          </Tooltip>
                        </td>
                      </tr>
                    );
                  })}
              </tbody>
            </table>
          </div>
          {count > 0 && (
            <PaginationBlock
              currentPage={currentPage}
              postsPerPage={postsPerPage}
              count={count}
              paginate={paginate}
              handleClick={handleClick}
              setCurrentPage={setCurrentPage}
              minPageNumberLimit={minPageNumberLimit}
              maxPageNumberLimit={maxPageNumberLimit}
            />
          )}
        </div>
      ) : (
        <NoData
          search={search}
          onClick={() => history.push("/create-print-credit")}
          addPermission={addPermission}
        />
      )}
      {show && (
        <PrintModal
          show={show}
          setShow={setShow}
          info={info}
          setInfo={setInfo}
        />
      )}
    </>
  );
};

export default Print;
